<template>
  <div class="course-manege">
    <div class="left">
      <div class="top">
        <Button @click="addShow = true" type="warning">新增题集</Button>
      </div>
      <div class="course-list">
        <Input @on-search="getWorkBookList" style="margin-bottom: 1rem;" search
               placeholder="输入题集名搜索"/>
        <ul>
          <li @click="courseSelect(item, key)" v-for="(item, key) in workbookList"
              :class="[selectIndex === key ? 'select' : '']" :key="item.workbookInfo.id">
            <div class="left">
              <img :src="item.workbookInfo.img || require('./../../../common/images/bg.png')" alt="">
            </div>
            <div class="right">
              <div class="top">
                <div class="left">
                  <Tag color="default">{{ item.workbookInfo.type }}</Tag>
                  <br>
                  <Tag color="primary">{{ item.workbookInfo.name }}</Tag>
                </div>
              </div>
              <div class="title ellipsis">
                {{ item.workbookInfo.title }}
              </div>
              <div class="bottom">
                <span style="float: left">{{ item.testList.length }} 题目</span>
                {{ item.workbookInfo.createdAt }}
              </div>
            </div>
          </li>
        </ul>
        <div class="no-thing" style="width: 100%;line-height: 5rem;display: flex;justify-content: center">
          <span>{{ workbookList.length === 0 ? '没有找到相关的题集哟，快快发布吧' : '到底了' }}</span>
        </div>
      </div>
    </div>
    <div class="right">
      <!--添加题集-->
      <section v-show="addShow" class="add-course">
        <Tabs value="name1">
          <TabPane label="新增题集" name="name1">
            <ul class="ul">
              <li>
                <div class="left">题集类别：</div>
                <div class="right">
                  <el-select v-model="type" placeholder="请选择">
                    <el-option
                        v-for="item in workbookTypeList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                  </el-select>
                </div>
              </li>
              <li>
                <div class="left">题集封面：</div>
                <div class="right">
                  <div class="img">
                    <img :src="img || require('./../../../common/images/bg.png')" alt="">
                    <input @change="selectFile"  type="file">
                  </div>
                </div>
              </li>
              <li>
                <div class="left">题集名称：</div>
                <div class="right">
                  <Input maxlength="30" v-model="name" placeholder="请设置题集名称"
                         style="width: 30rem"/>
                </div>
              </li>
              <li>
                <div class="left"></div>
                <div class="right">
                  <Button @click="addWorkBook" type="primary">新增题集</Button>
                </div>
              </li>
            </ul>
          </TabPane>
        </Tabs>
      </section>
      <!--题集详情|上传题目题集-->
      <section v-show="!addShow" class="course-detail">
        <div v-show="JSON.stringify(selectWorkBookInfo) !== '{}'" class="course-info">
          <Tabs value="name1">
            <TabPane label="题集信息" name="name1">
              <div style="position: relative;width: 100%;">
                <Button @click="delWorkBook" style="position: absolute;right: 1rem;top: 1rem;z-index: 2" type="error">
                  <Icon type="md-trash"/>
                </Button>
                <ul class="ul">
                  <li>
                    <div class="left">题集类别：</div>
                    <div class="right">
                      <el-select v-model="selectWorkBookInfo.type" placeholder="请选择">
                        <el-option
                            v-for="item in workbookTypeList"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                      </el-select>
                    </div>
                  </li>
                  <li>
                    <div class="left">题集封面：</div>
                    <div class="right">
                      <div class="img">
                        <img :src="selectWorkBookInfo.img || require('./../../../common/images/bg.png')" alt="">
                        <input @change="selectFileChange"  type="file">
                      </div>
                    </div>
                  </li>
                  <li>
                    <div class="left">题集名称：</div>
                    <div class="right">
                      <Input maxlength="50" v-model="selectWorkBookInfo.name" placeholder="请设置题集名称"
                             style="width: 30rem"/>
                    </div>
                  </li>
                  <li>
                    <div class="left"></div>
                    <div class="right">
                      <Button @click="updateWorkBook" type="primary">保存题集信息</Button>
                    </div>
                  </li>
                </ul>
              </div>
            </TabPane>
            <TabPane label="新增题目" name="name2">
              <ul class="ul">
                <li>
                  <div class="left">题目类别：</div>
                  <div class="right">
                    <el-select v-model="testInfo.type" placeholder="请选择">
                      <el-option
                          v-for="item in testTypeList"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                      </el-option>
                    </el-select>
                  </div>
                </li>
                <li>
                  <div class="left">题目标题：</div>
                  <div class="right">
                    <Input maxlength="50" v-model="testInfo.title" placeholder="请设置题目标题"
                           style="width: 30rem"/>
                  </div>
                </li>
                <li>
                  <div class="left">答案：</div>
                  <div class="right">
<!--                    单选、多选-->
                    <ul v-if="testInfo.type !== '简答题'">
                      <li v-for="(item, key) in optionList" :key="key" style="display: flex;margin-bottom: 1rem;">
                        <span style="margin-right: 1rem;">{{item.op}}</span>
                        <el-input
                            style="width: 30rem;" v-model="item.content" placeholder="请输入答案"></el-input>
                        <span style="margin-left: 1rem;">
                           <el-radio-group v-model="item.isTrue" size="small">
      <el-radio-button :label="1">正确</el-radio-button>
      <el-radio-button :label="0">错误</el-radio-button>
    </el-radio-group>
                        </span>
                      </li>
                    </ul>
<!--                    简答题-->
                    <ul v-else>
                      <li v-for="(item, key) in optionJDList" :key="key">
                        <el-input
                            style="width: 30rem;"
                            type="textarea"
                            :rows="2"
                            placeholder="请输入答案"
                            v-model="item.content">
                        </el-input>
                      </li>
                    </ul>
                  </div>
                </li>
                <li>
                  <div class="left"></div>
                  <div class="right">
                    <Button @click="addTest" type="primary">新增题目</Button>
                  </div>
                </li>
              </ul>
            </TabPane>
            <TabPane label="题目列表" name="name3">
              <section class="test-list">
                <Card v-for="(item, key) in testList" :key="item.id" :title="item.title" icon="ios-options" :padding="0"
                      shadow style="width: 30rem;margin: 1rem;">
                  <CellGroup>
                    <Cell :title="'题目类型'" :label="item.testInfo.type" />
                    <Cell :title="'题目名'" :label="item.testInfo.title"/>
                    <Cell :title="'发布时间'" :label="item.testInfo.createdAt"/>
                    <Cell title="选项">
                      答案：
                      <ul>
                        <li style="font-size: 1.2rem;" v-for="(i, k) in item.optionList" :key="k">
                          <span v-if="item.testInfo.type !== '简答题'">({{i.isTrue === 1 ? '√' : '×'}})</span>
                          {{i.content}}
                        </li>
                      </ul>
                    </Cell>
                    <Cell title="删除">
                      <Button @click="delTest(item, key)" slot="extra" size="small" type="error">
                        <Icon type="md-trash"/>
                      </Button>
                    </Cell>
                  </CellGroup>
                </Card>
                <div class="no-thing" style="width: 100%;line-height: 5rem;display: flex;justify-content: center">
                  <span>{{ testList.length === 0 ? '没有找到相关的题目哟，快快发布吧' : '到底了' }}</span>
                </div>
              </section>
            </TabPane>
          </Tabs>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import {
  addWorkBook,
  getWorkBookList,
  updateWorkBook,
  delWorkBook,
  addTest,
  getTestList,
  saveTest, delTest, uploadFile
} from "../../../api";
import config from './../../../config'
import {mapState} from "vuex";
import moment from 'moment'
import {testTypeList, workbookTypeList} from "@/data";

export default {
  name: "courseManage",
  data() {
    return {
      type: workbookTypeList[0].value,
      name: '',
      img: '',

      addShow: true,

      workbookList: [],
      selectIndex: -1,
      selectWorkBookInfo: {},
      testInfo: {
        title: '',
        type: testTypeList[0].value,
      },

      testTypeList,workbookTypeList,

      customColors: [
        {color: '#f56c6c', percentage: 20},
        {color: '#e6a23c', percentage: 40},
        {color: '#5cb87a', percentage: 60},
        {color: '#1989fa', percentage: 80},
        {color: '#6f7ad3', percentage: 100}
      ],
      testList: [],
      userList: [],
      // 多选、单选
      optionList: [
          // A
        {
          isTrue: 0,
          content: '',
          op: 'A',
        },
        // B
        {
          isTrue: 0,
          content: '',
          op: 'B',
        },
        // C
        {
          isTrue: 0,
          content: '',
          op: 'C',
        },
        // D
        {
          isTrue: 0,
          content: '',
          op: 'D',
        },
      ],
      // 简答题
      optionJDList: [
        {
          isTrue: 1,
          content: '',
        }
      ]
    }
  },
  computed: {
    ...mapState(['adminInfo'])
  },
  mounted() {
    this.getWorkBookList();
  },
  methods: {
    async saveTest(isShow, test) {
      // eslint-disable-next-line no-console
      console.log(isShow, test);
      let data = await saveTest({
        id: test.id,
        isShow
      });
      if (data.code === 200) {
        this.$Message.success(data.data);
      } else {
        this.$Message.error(data.data);
      }
    },
    delTest(test, key) {
      // eslint-disable-next-line no-console
      console.log(test);
      this.$Modal.confirm({
        title: '删除题目 ',
        content: '确认删除此题目吗？',
        okText: '删除',
        onOk: async () => {
          let data = await delTest({
            id: test.testInfo.id
          });
          if (data.code === 200) {
            this.testList.splice(key, 1);
            this.workbookList[this.selectIndex].testList.shift();
            this.$Message.success(data.data);
          } else {
            this.$Message.error(data.data);
          }
        }
      });
    },
    async getTestList() {
      let data = await getTestList({
        workbookId: this.selectWorkBookInfo.id,
      });
      if (data.code === 200) {
        let {testList} = data.result;
        // eslint-disable-next-line no-console
        console.log(testList);
        testList.forEach(item => {
          // let {testInfo, optionList} = item;
          item.testInfo.createdAt = moment(item.testInfo.createdAt).format('YYYY-MM-DD HH:mm:ss');
        });
        this.testList = testList;
      } else {
        this.$Message.error(data.data);
      }
    },
    async addTest() {
      let {type, title } = this.testInfo;
      let {id: workBookId} = this.selectWorkBookInfo;
      if (!title) {
        return this.$Message.warning('请设置题目标题');
      }
      let options;
      if(type === '单选题'){
        let trueList = this.optionList.filter(i => i.isTrue === 1);
        if(trueList.length === 0){
          return this.$Message.warning('请务必选择一个正确答案');
        }
        if(trueList.length > 1){
          return this.$Message.warning('单选题只能有一个正确答案');
        }
        let contentList = this.optionList.filter(i => i.content === '');
        if(contentList.length > 0){
          return this.$Message.warning('四个选项答案为必填项');
        }
        options = this.optionList;
        // eslint-disable-next-line no-empty
      }else if(type === '多选题'){
        let trueList = this.optionList.filter(i => i.isTrue === 1);
        if(trueList.length <= 1){
          return this.$Message.warning('请务必至少选择两个个正确答案');
        }
        let contentList = this.optionList.filter(i => i.content === '');
        if(contentList.length > 0){
          return this.$Message.warning('四个选项答案为必填项');
        }
        options = this.optionList;
      }else {
        if(!this.optionJDList[0].content){
          return this.$Message.warning('简答题答案必填');
        }
        options = this.optionJDList;
      }
      if (!workBookId) {
        return this.$Message.warning('未找到题集Id');
      }
      // eslint-disable-next-line no-console
      console.log(type, title, workBookId);
      let data = await addTest({
        type, title, workBookId, options
      });
      if (data.code === 200) {
        this.$Message.success(data.data);
        this.testInfo.title = '';
        this.optionList = [
          // A
          {
            isTrue: 0,
            content: '',
            op: 'A',
          },
          // B
          {
            isTrue: 0,
            content: '',
            op: 'B',
          },
          // C
          {
            isTrue: 0,
            content: '',
            op: 'C',
          },
          // D
          {
            isTrue: 0,
            content: '',
            op: 'D',
          },
        ];
        this.optionJDList = [
          {
            isTrue: 1,
            content: '',
          }
        ]

        this.workbookList[this.selectIndex].testList.push(data.result.test);
        this.getTestList();
      } else {
        this.$Message.error(data.data);
      }
    },
    async delWorkBook() {
      this.$Modal.confirm({
        title: '删除题集',
        content: '确认删除此题集吗？',
        okText: '删除',
        onOk: async () => {
          let data = await delWorkBook({
            id: this.selectWorkBookInfo.id
          });
          if (data.code === 200) {
            this.workbookList.splice(this.selectIndex, 1);
            this.$Message.success(data.data);
            this.addShow = true;
            this.selectIndex = -1;
            this.selectWorkBookInfo = {};
          } else {
            this.$Message.error(data.data);
          }
        }
      });
    },
    async updateWorkBook() {
      let {id, name, img, type,} = this.selectWorkBookInfo;
      if(!img){
        return this.$Message.warning('请上传题集封面图');
      }
      if (!name) {
        return this.$Message.warning('请设置题集名称');
      }
      let data = await updateWorkBook({
        id, type, name, img
      });
      if (data.code === 200) {
        this.$Message.success(data.data);
        this.getWorkBookList();
      } else {
        this.$Message.error(data.data);
      }

    },
    courseSelect(course, key) {
      this.addShow = false;
      this.selectIndex = key;
      this.selectWorkBookInfo = JSON.parse(JSON.stringify(course.workbookInfo));
      this.getTestList();
    },
    async getWorkBookList(keyWord = '') {
      let data = await getWorkBookList({
        keyWord
      });
      if (data.code === 200) {
        let workbookList = [];
        data.result.workbookList.forEach(item => {
          let {workbookInfo, testList} = item;
          workbookInfo.createdAt = moment(workbookInfo.createdAt).format('YY/MM/DD HH:mm');
          workbookList.push({
            workbookInfo,
            testList
          })
        });
        this.workbookList = workbookList;
        // eslint-disable-next-line no-console
        console.log(workbookList);
      } else {
        this.$Message.warning(data.data);
      }
    },
    async addWorkBook() {
      let {type, name, img} = this;
      if(!img){
        return this.$Message.warning('请上传题集封面图');
      }
      if (!name) {
        return this.$Message.warning('请设置题集名称');
      }
      let adminId = config.getCache('adminId');

      let data = await addWorkBook({
        type, name, adminId, img
      });
      if (data.code === 200) {
        this.name = '';
        this.$Message.success(data.data);
        this.getWorkBookList();
      } else {
        this.$Message.error(data.data);
      }
    },
    async selectFileChange(e) {
      let file = e.target.files[0];
      if (file.type !== 'image/jpeg' && file.type !== 'image/jpg' && file.type !== 'image/png' && file.type !== 'image/gif') {
        return this.$Message['error']({
          background: true,
          content: '您上传的文件：' + file.name + '格式不是png/jpg/jpeg/gif格式！请重新选择！'
        });
      }
      if (file.size > 2097152 * 5) {
        return this.$Message['error']({
          background: true,
          content: '您上传的文件：' + file.name + '大小超过10M！请重新选择！'
        });
      }
      let formData = new FormData();
      formData.append('file', file);
      let res = await uploadFile(formData);
      if (res.code === 200) {
        this.selectWorkBookInfo.img = res.result.url;
      } else {
        this.$Message['error']({
          background: true,
          content: '上传失败'
        });
      }
    },
    async selectFile(e) {
      let file = e.target.files[0];
      if (file.type !== 'image/jpeg' && file.type !== 'image/jpg' && file.type !== 'image/png' && file.type !== 'image/gif') {
        return this.$Message['error']({
          background: true,
          content: '您上传的文件：' + file.name + '格式不是png/jpg/jpeg/gif格式！请重新选择！'
        });
      }
      if (file.size > 2097152 * 5) {
        return this.$Message['error']({
          background: true,
          content: '您上传的文件：' + file.name + '大小超过10M！请重新选择！'
        });
      }
      let formData = new FormData();
      formData.append('file', file);
      let res = await uploadFile(formData);
      if (res.code === 200) {
        this.img = res.result.url;
      } else {
        this.$Message['error']({
          background: true,
          content: '上传失败'
        });
      }
    },
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
.test-list {
  display flex
  flex-wrap wrap
  justify-content space-around
}

.content {
  position relative
  width: 100%
  border: .1rem solid #fff
  padding-bottom: 3rem
  background-color: #fff

  > .editor {
    position: relative;
    width: 100%
    top: 0
    left: 0
    margin-top: 1rem
    flex 8
    border-radius .3rem
    z-index 0

    .toolbar {
      width: 100%
      max-height: 4rem
      font-size: 1.6rem
      border-bottom .1rem solid #fff
      line-height: 3rem
      z-index none !important
    }

    .textEditor {
      width: 100%
      height: 50rem
      z-index: 1 !important
      font-size: 1.6rem
      padding: 0 0 1rem 1rem
      overflow hidden
    }
  }
}

.course-manege {
  width: 100%
  height: 100%
  overflow: hidden;
  display flex

  > .left {
    flex 4
    max-width 30rem
    min-height 30rem
    height: 100%
    background-color: #fff
    overflow auto
    padding: 1rem

    > .top {
      width: 100%
      height: 5rem
      display flex
      align-items center
      justify-content center
    }

    > .course-list {
      width: 100%

      > ul {
        width: 100%

        > li {
          width: 100%
          height: 13rem
          border-bottom .1rem solid #f9f9f9
          cursor pointer
          padding: .5rem
          position relative
          overflow hidden
          transition all .3s

          &:hover {
            > .left {
              filter: blur(.07rem);
              -webkit-filter: blur(.07rem);
              -moz-filter: blur(.07rem);
              -ms-filter: blur(.07rem);
              -o-filter: blur(.07rem);
            }
          }

          &.select {
            > .left {
              filter: blur(0rem);
              -webkit-filter: blur(0rem);
              -moz-filter: blur(0rem);
              -ms-filter: blur(0rem);
              -o-filter: blur(0rem);
            }
          }

          > .left {
            width: 100%
            height: 100%
            transition all .3s

            > img {
              width: 100%
              height: 100%
              object-fit cover
            }
            filter: blur(.15rem);
            -webkit-filter: blur(.15rem);
            -moz-filter: blur(.15rem);
            -ms-filter: blur(.15rem);
            -o-filter: blur(.15rem);
          }

          > .right {
            position absolute
            left: 0
            top: 0
            width: 100%
            height: 100%
            padding: 1rem
            color white
            display flex
            align-items center
            justify-content center

            > .title {
              width: 100%
              font-weight: bold;
              text-align center
            }

            > .bottom {
              width: 100%
              position absolute
              bottom: 0
              left: 0
              padding: 1.2rem
              text-align right
              font-size: 1.2rem
              color #ebebeb
            }

            > .top {
              width: 100%
              position absolute
              left: 0
              top: 0
              padding: 1.2rem

              > .left {
                float left
              }

              > .right {
                float right
              }
            }
          }
        }
      }
    }
  }

  > .right {
    flex 6
    height: 100%
    overflow auto
    background-color: #f9f9f9
    padding: 1rem

    > .course-detail {
      width: 100%

      > .course-info {
        width: 100%
        position relative
      }
    }
  }
}
</style>
